// Module：新增出库信息
<template>
  <div class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="handleGoBack">返回</el-button>
      </div>
      <div class="fr">
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleNextStep('form')">下一步</el-button>
      </div>
    </el-row>
    <!-- Tip 折叠面板 -->
    <el-row>
      <el-collapse v-model="collapseValue" accordion>
        <el-collapse-item title="单据信息" name="1">
          <el-descriptions :column="2">
            <el-descriptions-item label="凭证日期">{{ baseInfo.documentDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="记账日期">{{ baseInfo.accountingDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="出货工厂">{{ baseInfo.shipmentFactoryName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="单据编号">{{ baseInfo.billNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="出货仓库">{{ baseInfo.shipmentWarehouseName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="移动类型">{{ baseInfo.movementType || '---' }}</el-descriptions-item>
            <el-descriptions-item label="成本中心">{{ baseInfo.costCenterName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="总科账目">{{ baseInfo.ledgerSubjects || '---' }}</el-descriptions-item>
            <el-descriptions-item label="资产">{{ baseInfo.asset || '---' }}</el-descriptions-item>
            <el-descriptions-item label="收货工厂">{{ baseInfo.receiptFactoryName || '---' }} {{ baseInfo.buyerTel || '---' }}</el-descriptions-item>
            <el-descriptions-item label="订单">{{ baseInfo.orderNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="收货仓库">{{ baseInfo.receiptWarehouseName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="项目">{{ baseInfo.project || '---' }}</el-descriptions-item>
            <el-descriptions-item :label="baseInfo.billType_dictText">{{ billNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="网络">{{ baseInfo.network || '---' }}</el-descriptions-item>
            <el-descriptions-item label="客户号">{{ baseInfo.custumerCode || '---' }}</el-descriptions-item>
            <el-descriptions-item label="转储单">{{ baseInfo.transferOrderNumber || '---' }}</el-descriptions-item>
          </el-descriptions>
        </el-collapse-item>
      </el-collapse>
    </el-row>
    <!-- Tip 表单 -->
    <el-form ref="form" :model="form" :rules="rules" inline label-width="80px" class="clearfix" @submit.native.prevent>
      <el-form-item prop="type" label="单据类型">
        <el-select v-model="form.type" filterable placeholder="单据类型" style="width: 178px;" class="filter-item">
          <el-option
            v-for="item in [{value: 1,label: '常规'},{value: 2,label: '战略'}]"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item prop="explanation" label="说明">
        <el-input v-model.trim="form.explanation" placeholder="请填写说明" class="filter-item" style="width: 178px;" />
      </el-form-item>
    </el-form>
    <el-form ref="filterItem" :model="query" inline class="top-filter" @submit.native.prevent>
      <el-form-item prop="materialCodeOrName">
        <el-input v-model.trim="query.materialCodeOrName" clearable placeholder="物料编码或名称" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
      </el-form-item>
      <el-button type="success" icon="el-icon-search" @click="searchInit">搜索</el-button>
      <el-button type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')">重置</el-button>
      <el-button v-if="!isCheckAll" type="warning" plain @click="checkAll">跨页全选</el-button>
      <el-button v-if="isCheckAll" type="warning" plain @click="reverseSelection">跨页反选</el-button>
    </el-form>
    <el-row>
      <table-com
        :ref="refName"
        :key="baseTableKey"
        :table-height-type="collapseValue ? 270 : 525"
        :loading="loading"
        :index="true"
        :selection="true"
        :row-key="rowKey"
        :selectable="selectable"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        :show-operate="false"
        :table-selection="tableSelection"
        @selectionChange="selectionChange"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getMaterialDetailApi, getReservationDetailApi, getReturnDetailApi } from '@/apis/modules/out-bound/outbound-information'
import { getOutsourceListApi } from '@/apis/modules/out-bound/outsourcing'
import { getBillTransferListApi } from '@/apis/modules/warehouse/discharge'
import { getSaveDetailApi, getSaveApi, saveStockOutMaterialsApi } from '@/apis/modules/out-bound/outbound-information-detail'
import { _debounce } from '@/utils'

export default {
  mixins: [paginationPage],
  data() {
    return {
      loading: false,
      query: {
        materialCodeOrName: ''
      },
      form: {
        type: '', // 单据类型
        explanation: '' // 说明
      },
      rules: {
        type: [
          { required: true, message: '请选择单据类型', trigger: 'change' }
        ]
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '物料组描述',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'needNum',
          label: '需求数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '单位',
          align: 'unit',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'word',
        //   prop: 'sendNum',
        //   label: '已发数量',
        //   align: 'unit',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        {
          module: 'word',
          prop: 'needDate',
          label: '需求日期',
          align: 'unit',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '库存数量',
          align: 'unit',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      supplierPullList: [],
      tableSelection: [],
      materialIds: [],
      detailApiParams: {
        billNumber: ''
      },
      collapseValue: '1', // 折叠面板绑定数据
      baseInfo: {}, // 基本信息
      baseTableKey: 1,
      rowKey: 'id',
      refName: 'baseTableRef',
      // 跨页全选标记
      isCheckAll: false
    }
  },
  computed: {
    // Tip 单据类型
    billType() {
      return this.$route.params.billType
    },
    // Tip 订单号
    billNumber() {
      return this.$route.params.billNumber
    }
  },
  watch: {
    collapseValue: {
      handler() {
        this.$nextTick(() => {
          this.baseTableKey++
        })
      }
    }
  },
  mounted() {
    // 生成出库单号并获取详情
    this.generateBillNumber()
    this.init()
  },
  methods: {
    // Tip 生成出库单号
    generateBillNumber() {
      return new Promise((resolve, reject) => {
        const params = {
          affiliatedBillNumber: this.billNumber,
          billType: this.billType
        }
        getSaveApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.detailApiParams.billNumber = data
            this.getDetail()
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 保存出库单物料
    saveStockOutMaterials() {
      const params = {
        ...this.form,
        ...this.detailApiParams, // 出库单号
        selectIds: this.tableSelection.map(i => i.id), // 选择出库物料
        allSelectedFlag: this.isCheckAll
      }
      return new Promise((resolve, reject) => {
        saveStockOutMaterialsApi({ ...params })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.materialIds = data
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // Tip 根据生成的出库单获取详情数据
    getDetail() {
      return new Promise((resolve, reject) => {
        const params = this.detailApiParams
        getSaveDetailApi({
          ...params
        })
          .then(res => {
            const { code, data } = res
            if (!code) {
              const { type, explanation } = data
              this.baseInfo = data
              this.form = { type, explanation }
              // 注意：SAP库0003、0009、0011入库和出库默认单据类型为战略
              const bool = ['0003', '0009', '0011'].includes(data.shipmentWarehouseName)
              if (bool) {
                this.form.type = 2
              } else {
                this.form.type = 1
              }
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // Tip 初始化来源单(预留单 | 领料单 | 退货单 | 外协单 | 调拨单)的物料列表
    init() {
      const api = {
        1: getReservationDetailApi, // 预留单
        2: getMaterialDetailApi, // 领料单
        3: getReturnDetailApi, // 退货单
        4: getOutsourceListApi, // 外协单
        5: getBillTransferListApi // 调拨单
      }[this.billType]
      if (!api) return
      this.loading = true
      return new Promise((resolve, reject) => {
        const params = {
          ...this.billType === '1' && { reservedNumber: this.billNumber },
          ...['2', '3'].includes(this.billType) && { requisitionNumber: this.billNumber },
          ...this.billType === '4' && { outsourceOutNumber: this.billNumber },
          ...this.billType === '5' && { transferNumber: this.billNumber },
          ...this.query
        }
        api({
          ...params,
          page: {
            ...this.pageInfo
          }
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
            // 实时选中
            const rowKey = this.rowKey
            const ids = this.tableSelection.map(i => i[rowKey])
            this.tableData.forEach(row => {
              function check(row) {
                const id = row[rowKey]
                return ids.some(i => i === id)
              }
              if (check(row)) {
                this.$nextTick(() => {
                  this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                })
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 表格点击事件回调
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    // Tip 控制表格某行是否可被勾选
    selectable(row) {
      return row.splitNum < row.needNum
    },
    // Tip 下一步 跳转《出库单据信息》
    handleNextStep: _debounce(function(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.saveStockOutMaterials()
            .then(res => {
              this.$store.dispatch('tagsView/delView', this.$route)
              this.$router.push({
                name: 'OutBoundInfoReceipt',
                params: {
                  billNumber: this.billNumber, // 单据编号
                  billType: this.billType, // 单据类型
                  warehouseOutOrderNo: this.detailApiParams.billNumber,
                  materialIds: JSON.stringify(this.materialIds)
                }
              })
            })
        } else {
          return false
        }
      })
    }, 800, false),
    // Tip 勾选用户发送回的事件
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // Tip 重置检索项表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 跨页全选
    checkAll() {
      const api = {
        1: getReservationDetailApi, // 预留单
        2: getMaterialDetailApi, // 领料单
        3: getReturnDetailApi, // 退货单
        4: getOutsourceListApi, // 外协单
        5: getBillTransferListApi // 调拨单
      }[this.billType]
      if (!api) return
      this.isCheckAll = true
      const params = {
        ...this.billType === '1' && { reservedNumber: this.billNumber },
        ...['2', '3'].includes(this.billType) && { requisitionNumber: this.billNumber },
        ...this.billType === '4' && { outsourceOutNumber: this.billNumber },
        ...this.billType === '5' && { transferNumber: this.billNumber },
        ...this.query,
        page: {
          current: 1, // 当前页码
          size: 10000 // 每页条数
        }
      }
      return new Promise((resolve, reject) => {
        api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list } = data
            if (list && Array.isArray(list) && list.length) {
              // 拆分数量小于等于需求数量 方可选择
              this.tableSelection = list.filter(row => row.splitNum <= row.needNum)
              if (!this.tableSelection.length) {
                this.isCheckAll = false
                this.$notify.warning({
                  title: '警告',
                  message: `暂不支持跨页全选`
                })
                return
              }

              // 初始化当前页面的勾选
              const rowKey = this.rowKey
              const ids = this.tableSelection.map(i => i[rowKey]) // 满足可被勾选条件的数据
              const curPageCheckedArr = [] // 当前页满足可被勾选条件de项
              this.tableData.forEach(row => {
                function check(row) {
                  const id = row[rowKey]
                  return ids.some(i => i === id)
                }
                if (check(row)) {
                  curPageCheckedArr.push(row)
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })

              // 除了当前页的其余页勾选
              this.tableSelection.forEach(row => {
                // 排除当前页已勾选的项,并选中其它可勾选项
                const ids = curPageCheckedArr.map(i => i[rowKey])
                if (!ids.includes(row[rowKey])) {
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 跨页反选
    reverseSelection() {
      this.isCheckAll = false
      this.$nextTick(() => {
        this.$refs[this.refName].$refs.multipleTable.clearSelection()
        this.tableSelection = []
      })
    },
    handleGoBack() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: 'OutBoundInformation'
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
